<template>
  <div name="contact">
    <CoverImage page="Contact" backgroundImage="backgnd_4.jpg" title="Contact Us" />

    <br />
    <br />
    <!-- Contact Us -->
    <v-container data-aos="fade-down" data-aos-duration="200">
      <h2>
        <strong>Contact Us</strong>
      </h2>
      <v-layout row wrap justify-space-around class="ma-2 mp-2">
        <v-flex sm10 md3>
          <v-card flat>
            <v-card-subtitle class="mb-1 font-weight-bold">Address</v-card-subtitle>
            <v-card-text class="display-0 text--secondary">
              Plot no. 27, "Ranjan", Navsahydri society,
              <br />Karvenagar,
              <br />Near Tol Hospital,
              <br />Pune - 411052
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm10 md3>
          <v-card flat>
            <v-card-subtitle class="mb-1 font-weight-bold">Phone</v-card-subtitle>
            <v-card-text class="display-0 text--secondary">
              <a href="tel://+91-9004052169">+91 9004052169</a>
              <br />
              <a href="tel://+91-9011002430">+91 9011002430</a>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm10 md3>
          <v-card flat>
            <v-card-subtitle class="mb-1 font-weight-bold">Email</v-card-subtitle>
            <v-card-text class="display-0 text--secondary">
              <a href="mailto:info@travelfreakvacations.com">info@travelfreakvacations.com</a>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex sm10 md3>
          <v-card flat>
            <v-card-subtitle class="mb-1 font-weight-bold">Website</v-card-subtitle>
            <v-card-text class="display-0 text--secondary">
              <a href="https://www.travelfreakvacations.com">www.travelfreakvacations.com</a>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import CoverImage from "@/components/common/CoverImage.vue";

export default {
  name: "Contact",
  head: {
    title: "Contact",
    link: [
      {
        rel: "canonical",
        href: "https://www.travelfreakvacations.com/contact"
      }
    ],
    meta: [
      {
        name: "description",
        content:
          "We have a network across 7 continents. Connect with us, and we will take care of all your travel needs."
      },
      // OpenGraph data (Most widely used)
      { property: "og:title", content: "Contact - Travelfreak Vacations" },
      { property: "og:site_name", content: "Travelfreak Vacations" },
      // The list of types is available here: http://ogp.me/#types
      { property: "og:type", content: "website" },
      // Should be the same as your canonical link, see below.
      {
        property: "og:url",
        content: "https://www.travelfreakvacations.com/contact"
      },
      {
        property: "og:image",
        content: "https://www.travelfreakvacations.com/images/tfv-logo.png"
      },
      // Often the same as your meta description, but not always.
      {
        property: "og:description",
        content:
          "We have a network across 7 continents. Connect with us, and we will take care of all your travel needs."
      }
    ]
  },
  components: {
    CoverImage
  }
};
</script>



